body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.base-container{
    width:100%;
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.header{
    font-size:24px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.content{
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}
.image{
    width: 21em;
}
img{
    width: 100%;
    height: 100%;
}
.form{
    margin-top: 2em;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    
}
.form-group{
         display: -webkit-flex;
         display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-align-items: flex-start;
                align-items: flex-start;
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        }
label{
    font: size 20px;
}
input{
    margin-top:60px;
    min-width:18em;
    height: 37px;
    padding:0px 10px;
    font-size:16px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    background-color: #f3f3f3;
    border:0;
    border-radius:4px;
    margin-bottom: 31px;
    transition: all 250ms ease-in-out;
}

:focus{
outline: none;
box-shadow: 0px 0px 12px 0.8px rgba(0, 238, 135, 0.493);
}
.footer{
    margin-top:3em;
}

.btn{
    font-size: 21px;
    padding:5px 20px;
    border:0;
    background-color: greenyellow;
    color: honeydew;
    border-radius: 3px;
    cursor: pointer;
}
.bth :hover{
    background-color: green;
}
.bth:focus{
outline:none;
}
